import React, { Component } from 'react';
import { Tekst } from './forms';
import { skrotTekstu } from '../utils/utils';
import api from '../utils/api';

export class ZalacznikSkrot extends Component {

    ikony() {
        const onShow= (e) => this.props.onShow(e, this.props.lp);
        return (
            <ul className="nav navbar-nav przyciski">
                <li onClick={onShow}>
                    <span className="glyphicon glyphicon-pencil" aria-hidden="true"></span>
                </li>
            </ul>
        );
    }

    render() {
        const props= this.props;
        // const onShow= (e) => props.onShow(e, props.lp);
        const plik_url= api.zalacznik_url(props.poz.id);

        return (
            <div className="panel panel-default">
                <div className="panel-heading">
                    {this.ikony()}
                    <a href={plik_url}>{props.poz.nazwa}</a>
                </div>

                <div className="panel-body">
                    {skrotTekstu(props.poz.opis)}
                </div>
            </div>
        );
    }
}

export const Zalacznik = (props) => {
    
    const {form, onFileChange, onSave, onDelete, onCancel}= props;

    return (
        <div className="panel panel-default">
            <div className="panel-body">
            
                <div className="form-group">
                    <label htmlFor="nazwa_pliku" className="control-label">Plik załącznika</label>
                    <div className="input-group">
                        <label className="input-group-btn">
                            <span className="btn btn-primary">
                                Wybór pliku <input type="file" onChange={onFileChange} />
                            </span>
                        </label>
                        <input type="text" id="nazwa_pliku" value={form.nazwa} className="form-control" readOnly/>
                    </div>
                </div>

                <Tekst nazwa="opis" rows="5" etykieta="Opis zawartości załącznika" value={form.opis} {...props}/>

                <div className="przyciski przyciski2">
                    <button className="btn btn-default" type="submit" value="Save" onClick={onSave}>Zapisz</button>
                    <button className="btn btn-default" type="submit" value="Delete" onClick={onDelete}>Usuń</button>            
                    <button className="btn btn-default cancel" value="Cancel" onClick={onCancel}>Rezygnuj ze zmian</button>
                </div>
            </div>
        </div>
    );
};
