import React, { Component } from 'react';
import api from './utils/api';
import { Zalacznik, ZalacznikSkrot } from './components/Zalacznik';

class Dodatkowe extends Component {

    constructor(props) {
        super(props);
        this.state= {
            readonly: true,
            view: 'calosc'
        };

        this.old_y= null;
        this.new_y= null;

        for(const f of Object.getOwnPropertyNames(Object.getPrototypeOf(this))) {
            if (f.startsWith('handle') || f.startsWith('toggle')) 
                this[f]= this[f].bind(this);
        }
    }

    async componentDidMount() {
        const result= await api.zalacznik_get();
        const pozycje= result.data.pozycje;
        this.setState({ pozycje, lp: pozycje.length });
    }

    handleDodaj(e) {
        e.preventDefault();
        // Przejście do edycji nowej pozycji uszczegółowienia
        this.old_y= window.pageYOffset;
        this.new_y= 0;

        this.setState(() => {
            return {lp: -1, nazwa: '', opis: '', view: 'zalacznik', readonly: false};
        });
    }

    handleShow(e, lp) {
        e.preventDefault();

        this.old_y= window.pageYOffset;
        this.new_y= 0;

        this.setState((state) => {
            const { nazwa, opis } = state.pozycje[lp];
            return {view: 'zalacznik', readonly: false, lp, nazwa, opis};
        });
    }

    handleFileChange(e) {
        this.setState({
            selectedFile: e.target.files[0],
            nazwa: e.target.files[0].name,
            loaded: 0,
        });
    }

    handleBack(e) {
        this.new_y= this.old_y;
        this.old_y= void 0;
        e.preventDefault();

        this.setState(() => {
            this.setState({view: 'calosc', lp: -1});
        });
    }

    async handleSave(e) {
        e.preventDefault();
        this.new_y= 0;

        const data = new FormData();
        const plik= this.state.selectedFile;
        const nazwa= plik? plik.name : void 0;
        data.append('file', plik, nazwa);

        const { pozycje, lp, opis } = this.state;
        const id= (lp>=0)?pozycje[lp].id: 0;
        const form= {id, opis};

        data.append('form', JSON.stringify(form));
    
        const res= await api.zalacznik_save(id, data);

        const poz= res.data.poz;
        if (lp>=0)
            pozycje[lp]= poz;
        else
            pozycje.push(poz);

        this.setState({readonly: true, view: 'calosc', pozycje});
    }

    handleDelete(e) {
        this.new_y= this.old_y;
        this.old_y= void 0;
        e.preventDefault();
        let poz= void 0;

        this.setState(({lp, pozycje}) => {
            if (lp>=0) {
                poz= pozycje.splice(lp, 1);
            }

            return {pozycje, view: 'calosc', readonly: true};
        },
        () => {
            api.zalacznik_del(poz);
        });
    }

    handleCancel(e) {
        e.preventDefault();

        this.new_y= 0;

        this.setState((state) => {
            const lp= this.state.lp;
            if (lp>=0) {
                const { nazwa, opis } = state.pozycje[lp];
                this.setState({readonly: true, nazwa, opis, view: 'calosc'});
            }
            else {
                this.new_y= this.old_y;
                this.setState({readonly: true, view: 'calosc'});
            }
        });
    }

    zalacznik() {
        return (
            <Zalacznik 
                form={this.state}

                onFileChange={this.handleFileChange}
                onBack={this.handleBack}
                onSave={this.handleSave}
                onDelete={this.handleDelete}
                onCancel={this.handleCancel}

                onMount={this.handleMount}
            />
        );
    }

    zalaczniki() {
        if (!this.state.pozycje)
            return '';

        return this.state.pozycje.map((poz, lp) => 
            <ZalacznikSkrot key={poz.id} lp={lp} poz={poz} onShow={this.handleShow}/>
        );
    }

    calosc() {
        return (
            
            <div className="panel-group" id="accordion" role="tablist" aria-multiselectable="true">
                
                <div className="list-group">
                    {this.zalaczniki()}
                </div>
                    
                <button className="btn btn-default" onClick={this.handleDodaj}>Dodaj załącznik</button>

            </div>      
        );   
    }

    view() {
        let v= null;
        switch(this.state.view) {
        case 'zalacznik': v= this.zalacznik(); break;
        default:
            v= this.calosc();
        }
        return v;
    }

    handleChange(e) {
        // preventing checkbox state change on readonly state
        if (e.target.type === 'checkbox' && this.state.readonly)
            return;

        let change = { [e.target.name]: e.target.type === 'checkbox' ? e.target.checked : e.target.value };

        if (e.target.name === 'opis')
            Object.assign(change, {tah: e.target.scrollHeight});

        this.setState(change);
    }

    render() {
        return (
            <div className="panel-body">
                <form onChange={this.handleChange} autoComplete="off">
                    {this.view()}
                </form>
            </div>
        );
    }

}

export default Dodatkowe;
